	module.exports = {"data":{"markdownRemark":{"html":"<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1200px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 70.70484581497797%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"sketch\"\n        title=\"\"\n        src=\"/static/b2e8c69aaed2d39845af12045cecd9f3-ad853.png\"\n        srcset=\"/static/b2e8c69aaed2d39845af12045cecd9f3-1fef5.png 300w,\n/static/b2e8c69aaed2d39845af12045cecd9f3-cc110.png 600w,\n/static/b2e8c69aaed2d39845af12045cecd9f3-ad853.png 1200w,\n/static/b2e8c69aaed2d39845af12045cecd9f3-18f9f.png 1362w\"\n        sizes=\"(max-width: 1200px) 100vw, 1200px\"\n      />\n    </span>\n  </span>\n  </p>\n<h1>Endless Horizons: Images for Machine Learning &#x26; Leisure, 2017</h1>\n<h2>Concept</h2>\n<p>While scraping panoramic images of the US coast from the NDBC’s network of camera-enabled buoys, we wondered how a computer might interpret or “see” these many beautiful, and at times tumultuous, vistas of virtually endless sea and sky. Using computer vision, each panorama has been computationally “reduced” to a series of simple color gradients and lines. The panoramic photos are only taken once per hour, so the video piece fills in and smooths the gaps of the undocumented passage of time. The panoramic views have then been individually wrapped into circular forms to fit the projection space of the underside of the Santa Fe Railyard Plaza water tower. Looking upward, the viewer is immersed in a large-scale circumference of fluctuating sea and sky, and left to question the relationship between fresh water, the desert landscape and shifting sea levels. </p>\n<h2>Logistics</h2>\n<p>The work is a site-specific installation featuring a single channel video. The desired location is the Santa Fe Railyard Plaza water tower. We would place a secured projector on the ground pointed at the ceiling of the public space under the water tower. The projection will be driven by a lower power micro-computer (Raspberry PI). If the water tower is not a feasible location, there are alternate sites in the Santa Fe Railyard park that would work well (see proposal sketch).</p>\n<p>If there is a power supply in the water tower, we would run an extension cable - with a cord cover - to the projector and micro-computer. Alternatively, we would place a portable power station next to the projector.</p>\n<p>The projector and micro-computer would be placed within a projector enclosure and locked to a nearby structure (e.g. a leg of the water tower).</p>\n<p>The minimum number of artists required to travel is two.</p>\n<h2>CV</h2>\n<p><a href=\"/encoded-objects-resume.pdf\">Resume</a></p>\n<h2>Images of Proposed Work</h2>\n<p>Desired location:</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1200px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 70.70484581497797%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"proposal water tower\"\n        title=\"\"\n        src=\"/static/7e03ae0e3f80ac3451f3142b78c4ebff-772a8.jpg\"\n        srcset=\"/static/7e03ae0e3f80ac3451f3142b78c4ebff-7dd87.jpg 300w,\n/static/7e03ae0e3f80ac3451f3142b78c4ebff-0af02.jpg 600w,\n/static/7e03ae0e3f80ac3451f3142b78c4ebff-772a8.jpg 1200w,\n/static/7e03ae0e3f80ac3451f3142b78c4ebff-d00da.jpg 1362w\"\n        sizes=\"(max-width: 1200px) 100vw, 1200px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>Still from the projection:</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1200px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 56.25%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"proposal still\"\n        title=\"\"\n        src=\"/static/a6384b30eb4c20ddebbe52b4a4e76396-772a8.jpg\"\n        srcset=\"/static/a6384b30eb4c20ddebbe52b4a4e76396-7dd87.jpg 300w,\n/static/a6384b30eb4c20ddebbe52b4a4e76396-0af02.jpg 600w,\n/static/a6384b30eb4c20ddebbe52b4a4e76396-772a8.jpg 1200w,\n/static/a6384b30eb4c20ddebbe52b4a4e76396-60291.jpg 1800w,\n/static/a6384b30eb4c20ddebbe52b4a4e76396-8a974.jpg 1920w\"\n        sizes=\"(max-width: 1200px) 100vw, 1200px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>Excerpt from the projection:</p>\n<div>\n          <div\n            class=\"gatsby-resp-iframe-wrapper\"\n            style=\"padding-bottom: 56.25%; position: relative; height: 0; overflow: hidden;\"\n          >\n            <html><head></head><body><iframe src=\"https://player.vimeo.com/video/241415103\" frameborder=\"0\" webkitallowfullscreen=\"\" mozallowfullscreen=\"\" allowfullscreen style=\"\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n          \"></iframe></body></html>\n          </div>\n          </div>\n<p>Alternate location:</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1200px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 53.037542662116046%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"proposal alternate location\"\n        title=\"\"\n        src=\"/static/514e92d1518dfe76b3c6b56d31fb2e9d-772a8.jpg\"\n        srcset=\"/static/514e92d1518dfe76b3c6b56d31fb2e9d-7dd87.jpg 300w,\n/static/514e92d1518dfe76b3c6b56d31fb2e9d-0af02.jpg 600w,\n/static/514e92d1518dfe76b3c6b56d31fb2e9d-772a8.jpg 1200w,\n/static/514e92d1518dfe76b3c6b56d31fb2e9d-b0a64.jpg 1465w\"\n        sizes=\"(max-width: 1200px) 100vw, 1200px\"\n      />\n    </span>\n  </span>\n  </p>\n<h2>Floor Plan</h2>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1200px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 70.42483660130719%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"floor plan\"\n        title=\"\"\n        src=\"/static/b18770ce2e4276cdabcc6e2f22a247fe-ad853.png\"\n        srcset=\"/static/b18770ce2e4276cdabcc6e2f22a247fe-1fef5.png 300w,\n/static/b18770ce2e4276cdabcc6e2f22a247fe-cc110.png 600w,\n/static/b18770ce2e4276cdabcc6e2f22a247fe-ad853.png 1200w,\n/static/b18770ce2e4276cdabcc6e2f22a247fe-de57a.png 1800w,\n/static/b18770ce2e4276cdabcc6e2f22a247fe-7b84b.png 1836w\"\n        sizes=\"(max-width: 1200px) 100vw, 1200px\"\n      />\n    </span>\n  </span>\n  </p>\n<h2>Supplies</h2>\n<p><strong>Equipment from Festival</strong></p>\n<ul>\n<li>A short throw projector (5000 lumens or greater)</li>\n<li>Power supply </li>\n<li>Lock</li>\n<li>Projector enclosure</li>\n</ul>\n<p><strong>Equipment from Us</strong></p>\n<ul>\n<li>Micro-computer (Raspberry PI)</li>\n<li>HDMI Cable</li>\n<li>Extension cord</li>\n<li>Power strip</li>\n</ul>","frontmatter":{"date":"September 02, 2017","title":"Proposal for Currents 2017","subtitle":""}}},"pathContext":{}}

